'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import Script from 'next/script';

interface Location {
  address: string;
  coordinates: [number, number];
}

interface TripFormData {
  departure_location: string;
  destination_location: string;
  departure_coords: string;
  destination_coords: string;
  departure_time: string;
  available_seats: number;
  price: number;
  description: string;
}

declare global {
  interface Window {
    ymaps: any;
  }
}

export default function CreateTripPage() {
  const router = useRouter();
  const [map, setMap] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [route, setRoute] = useState<any>(null);
  const [departureMarker, setDepartureMarker] = useState<any>(null);
  const [destinationMarker, setDestinationMarker] = useState<any>(null);
  const [isSelectingDeparture, setIsSelectingDeparture] = useState(false);
  const [isSelectingDestination, setIsSelectingDestination] = useState(false);
  const [formData, setFormData] = useState<TripFormData>({
    departure_location: '',
    destination_location: '',
    departure_coords: '',
    destination_coords: '',
    departure_time: '',
    available_seats: 1,
    price: 0,
    description: '',
  });

  useEffect(() => {
    // Initialize map after Yandex Maps API is loaded
    if (window.ymaps) {
      window.ymaps.ready(() => {
        const newMap = new window.ymaps.Map('map', {
          center: [55.76, 37.64], // Moscow coordinates
          zoom: 10
        });

        // Add click listener to map
        newMap.events.add('click', async (e: any) => {
          const coords = e.get('coords');
          if (isSelectingDeparture) {
            await handleMapClick('departure', coords);
            setIsSelectingDeparture(false);
          } else if (isSelectingDestination) {
            await handleMapClick('destination', coords);
            setIsSelectingDestination(false);
          }
        });

        setMap(newMap);
      });
    }
  }, [isSelectingDeparture, isSelectingDestination]);

  const handleMapClick = async (type: 'departure' | 'destination', coords: [number, number]) => {
    try {
      // Reverse geocode the clicked coordinates
      const response = await fetch(`http://localhost:8000/api/trips/geocode_address/`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('access_token')}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ coordinates: coords }),
      });

      if (!response.ok) {
        throw new Error('Failed to geocode coordinates');
      }

      const data = await response.json();
      const location = {
        address: data.address,
        coordinates: coords,
      };

      // Update markers and form data
      if (type === 'departure') {
        if (departureMarker) {
          map.geoObjects.remove(departureMarker);
        }
        const newMarker = new window.ymaps.Placemark(coords, {
          balloonContent: data.address,
          iconCaption: 'Место отправления'
        }, {
          preset: 'islands#blueCircleDotIcon'
        });
        map.geoObjects.add(newMarker);
        setDepartureMarker(newMarker);
        setFormData(prev => ({
          ...prev,
          departure_location: data.address,
          departure_coords: coords.join(',')
        }));
      } else {
        if (destinationMarker) {
          map.geoObjects.remove(destinationMarker);
        }
        const newMarker = new window.ymaps.Placemark(coords, {
          balloonContent: data.address,
          iconCaption: 'Место назначения'
        }, {
          preset: 'islands#redCircleDotIcon'
        });
        map.geoObjects.add(newMarker);
        setDestinationMarker(newMarker);
        setFormData(prev => ({
          ...prev,
          destination_location: data.address,
          destination_coords: coords.join(',')
        }));
      }

      // If both locations are set, draw route
      if (formData.departure_coords && (type === 'destination' || formData.destination_coords) && (type === 'departure')) {
        drawRoute();
      }
    } catch (err) {
      console.error('Error handling map click:', err);
      setError('Ошибка при определении адреса');
    }
  };

  const geocodeAddress = async (address: string): Promise<Location | null> => {
    try {
      const response = await fetch(`http://localhost:8000/api/trips/geocode_address/`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('access_token')}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ address }),
      });

      if (!response.ok) {
        throw new Error('Failed to geocode address');
      }

      const data = await response.json();
      return {
        address: data.address,
        coordinates: data.coordinates,
      };
    } catch (err) {
      console.error('Error geocoding address:', err);
      return null;
    }
  };

  const handleAddressChange = async (field: 'departure_location' | 'destination_location', value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));

    if (value.length > 3) {
      const location = await geocodeAddress(value);
      if (location) {
        const coordsField = field === 'departure_location' ? 'departure_coords' : 'destination_coords';
        setFormData(prev => ({
          ...prev,
          [field]: location.address,
          [coordsField]: location.coordinates.join(','),
        }));

        // Update map
        if (map) {
          map.geoObjects.remove(route);
          const placemark = new window.ymaps.Placemark(location.coordinates, {
            balloonContent: location.address
          });
          map.geoObjects.add(placemark);
          map.setCenter(location.coordinates, 15);

          // If both addresses are set, draw route
          if (formData.departure_coords && formData.destination_coords) {
            drawRoute();
          }
        }
      }
    }
  };

  const drawRoute = async () => {
    if (!map || !formData.departure_coords || !formData.destination_coords) return;

    const [startLat, startLng] = formData.departure_coords.split(',');
    const [endLat, endLng] = formData.destination_coords.split(',');

    try {
      const response = await fetch(`http://localhost:8000/api/trips/calculate_route/`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('access_token')}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          start_coords: [startLat, startLng],
          end_coords: [endLat, endLng],
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to calculate route');
      }

      const data = await response.json();
      
      // Draw route on map
      const multiRoute = new window.ymaps.multiRouter.MultiRoute({
        referencePoints: [
          [startLat, startLng],
          [endLat, endLng],
        ],
        params: {
          routingMode: 'auto'
        }
      });

      map.geoObjects.remove(route);
      map.geoObjects.add(multiRoute);
      setRoute(multiRoute);

      // Fit map to show entire route
      multiRoute.events.add('routeready', () => {
        map.setBounds(multiRoute.getBounds());
      });
    } catch (err) {
      console.error('Error calculating route:', err);
      setError('Ошибка при расчете маршрута');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    try {
      const token = localStorage.getItem('access_token');
      if (!token) {
        router.push('/auth/login');
        return;
      }

      const response = await fetch('http://localhost:8000/api/trips/', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        throw new Error('Failed to create trip');
      }

      router.push('/trips');
    } catch (err) {
      setError('Ошибка при создании поездки');
      console.error('Error creating trip:', err);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      <Script 
        src={`https://api-maps.yandex.ru/2.1/?apikey=${process.env.NEXT_PUBLIC_YANDEX_MAPS_API_KEY}&lang=ru_RU`} 
        strategy="beforeInteractive"
      />
      
      <div className="min-h-screen bg-gray-100 py-12">
        <div className="max-w-7xl mx-auto sm:px-6 lg:px-8">
          <div className="bg-white shadow-xl rounded-lg overflow-hidden">
            <div className="px-4 py-5 sm:px-6 bg-indigo-600">
              <h3 className="text-lg leading-6 font-medium text-white">
                Создание новой поездки
              </h3>
            </div>

            <div className="px-4 py-5 sm:p-6">
              {error && (
                <div className="mb-4 p-4 bg-red-50 border border-red-200 rounded-md">
                  <p className="text-sm text-red-600">{error}</p>
                </div>
              )}

              <div className="mb-6">
                <div id="map" style={{ width: '100%', height: '400px' }} />
                <div className="mt-2 flex gap-2">
                  <button
                    type="button"
                    onClick={() => {
                      setIsSelectingDeparture(true);
                      setIsSelectingDestination(false);
                    }}
                    className={`px-4 py-2 text-sm font-medium rounded-md ${
                      isSelectingDeparture
                        ? 'bg-blue-600 text-white'
                        : 'bg-white text-gray-700 border border-gray-300'
                    }`}
                  >
                    Выбрать место отправления
                  </button>
                  <button
                    type="button"
                    onClick={() => {
                      setIsSelectingDestination(true);
                      setIsSelectingDeparture(false);
                    }}
                    className={`px-4 py-2 text-sm font-medium rounded-md ${
                      isSelectingDestination
                        ? 'bg-blue-600 text-white'
                        : 'bg-white text-gray-700 border border-gray-300'
                    }`}
                  >
                    Выбрать место назначения
                  </button>
                </div>
              </div>

              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
                  <div>
                    <label className="block text-sm font-medium text-gray-700">
                      Место отправления
                    </label>
                    <input
                      type="text"
                      value={formData.departure_location}
                      onChange={(e) => handleAddressChange('departure_location', e.target.value)}
                      className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm text-black"
                      placeholder="Выберите на карте или введите адрес"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700">
                      Место назначения
                    </label>
                    <input
                      type="text"
                      value={formData.destination_location}
                      onChange={(e) => handleAddressChange('destination_location', e.target.value)}
                      className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm text-black"
                      placeholder="Выберите на карте или введите адрес"
                      required
                    />
                  </div>
                </div>

                <div className="grid grid-cols-1 gap-6 sm:grid-cols-3">
                  <div>
                    <label className="block text-sm font-medium text-gray-700">
                      Дата и время отправления
                    </label>
                    <input
                      type="datetime-local"
                      value={formData.departure_time}
                      onChange={(e) => setFormData(prev => ({ ...prev, departure_time: e.target.value }))}
                      className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm text-black"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700">
                      Количество мест
                    </label>
                    <input
                      type="number"
                      min="1"
                      value={formData.available_seats}
                      onChange={(e) => setFormData(prev => ({ ...prev, available_seats: parseInt(e.target.value) }))}
                      className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm text-black"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700">
                      Цена за место
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={formData.price}
                      onChange={(e) => setFormData(prev => ({ ...prev, price: parseInt(e.target.value) }))}
                      className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm text-black"
                      required
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Описание поездки
                  </label>
                  <textarea
                    value={formData.description}
                    onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
                    rows={4}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm text-black"
                    placeholder="Дополнительная информация о поездке..."
                  />
                </div>

                <div className="flex justify-end space-x-3">
                  <button
                    type="button"
                    onClick={() => router.push('/trips')}
                    className="inline-flex items-center px-4 py-2 border border-gray-300 shadow-sm text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                  >
                    Отмена
                  </button>
                  <button
                    type="submit"
                    disabled={isLoading}
                    className={`inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 ${
                      isLoading ? 'opacity-75 cursor-not-allowed' : ''
                    }`}
                  >
                    {isLoading ? (
                      <>
                        <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                        </svg>
                        Создание...
                      </>
                    ) : (
                      'Создать поездку'
                    )}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
} 